<template>
  <div class="h-screen p-3">
    <div class="bg-white border border-gray-500 rounded h-[96vh]
    max-w-[360px] max-h-[440px] mx-auto">
      
      <div class="grid grid-cols-4 mx-2 gap-2 items-center text-center">
        <!-- first row -->
        <div class="monitor rounded mt-2 border border-gray-500
        text-5xl px-2 pb-2 bg-gray-300 col-span-4 text-left">
          {{expbar}}
        </div>
        <!-- second row -->
        <div class="col-span-3 text-left ml-3 text-lg">
          Fucking Calc Co.
        </div>
        <button @click="clear()" class="bg-gray-100 rounded p-3 border border-gray-500 shadow hover:bg-gray-200">
          AC
        </button>
        <hr class="col-span-4">
        <button @click="append('1')" class="calckey">1</button>
        <button @click="append('2')" class="calckey">2</button>
        <button @click="append('3')" class="calckey">3</button>
        <button @click="operate('*')" class="operand">ร</button>
        <button @click="append('4')" class="calckey">4</button>
        <button @click="append('5')" class="calckey">5</button>
        <button @click="append('6')" class="calckey">6</button>
        <button @click="operate('/')" class="operand">รท</button>
        <button @click="append('7')" class="calckey">7</button>
        <button @click="append('8')" class="calckey">8</button>
        <button @click="append('9')" class="calckey">9</button>
        <button @click="operate('-')" class="operand">-</button>
        <button @click="append('0')" class="calckey">0</button>
        <button @click="append('.')" class="calckey">.</button>
        <button @click='operate("equals")' class="operand">=</button>
        <button @click="operate('+')" class="operand ">+</button>
      </div>
      
    </div>  
  </div>
  
</template>

<script>
import {evaluate} from 'mathjs'
export default {
  name: 'SimpleCalc',
  data(){
    return{
      expbar:'0',
      expression: '',
      operator: '',
    }
  },
  methods:{
    append(char){
      // some code here
      // alert(char);
      if(char == '.'){
        if(!this.expbar.includes(char)){
          this.expbar += '.';
        }
      }else{
        if(this.operator === ''){
          if(this.expbar == "0"){
            this.expbar = char;
          }else{
            this.expbar += char;
          }
        }else{
          this.expbar = char;
          this.operator = '';
        }
      }
    },
    operate(operator){
      if(operator == 'equals'){
        console.log(this.expression);

        if(this.operator == ""){
          this.expression += this.expbar;
        }else{
          //console.log("before"+this.expression);
          this.expression = this.expression.slice(0,-1);
          //console.log("after"+this.expression);
        }
        let answer = evaluate(this.expression);
        this.expbar = answer;
        this.operator = 'done';
        this.expression = '';
      }else{
        if(this.operator == '' || this.operator == 'done'){
          this.operator = operator;
          this.expression += this.expbar + this.operator;
        }
      }
    },
    incr(){
      this.expbar += 1;
    },
    clear(){
      this.expbar = 0;
      this.expression = '';
      this.operator = '';
    }
  }
}
</script>

<style scoped>


  @font-face{
    font-family: "7digits";
    src: url(../assets/digital-7.ttf) format("truetype");
  }
  .monitor{
    font-family:"7digits";
  }
  .calckey{
    @apply bg-gray-100 rounded py-5 px-3 border border-gray-500 shadow
    hover:bg-gray-200;
  }
  .operand{
    @apply bg-gray-300 rounded py-5 px-3 border border-gray-500 shadow font-bold hover:bg-gray-400;
  }
</style>
